<template>
    <div>
        <div class="score">
            <div class="score-progress">
                <progress-bar
                    :options="options"
                    :value="Math.floor(results.percentage)"
                    duration="2000"
                />
            </div>
            <div class="info">
                <h3 class="title">
                    You got {{ results.total_correct }} out of
                    {{ results.total_questions }} right.
                </h3>
                <p v-if="Math.floor(results.percentage) == 100" class="desc">
                    Congrats, You passed the test! Click below to share your
                    result.
                </p>
                <p
                    v-if="
                        Math.floor(results.percentage) > 50 &&
                            Math.floor(results.percentage) <= 90
                    "
                    class="desc"
                >
                    Bummer! You were so close! Click below to rewatch the video
                    and retake the quiz.
                </p>
                <p
                    v-if="
                        Math.floor(results.percentage) > 20 &&
                            Math.floor(results.percentage) <= 50
                    "
                    class="desc"
                >
                    Not Bad, but you can do it better! Click below to rewatch
                    the video and retake the quiz.
                </p>
                <p v-if="Math.floor(results.percentage) < 20" class="desc">
                    Not so good. Click below to rewatch the video and retake the
                    quiz.
                </p>
                <div class="d-flex">
                    <a :href="`/lessons`" class="gold-btn">
                        Rewatch Video
                    </a>
                </div>
            </div>
        </div>
        <div class="results">
            <span class="title">YOUR RESULTS</span>
            <div class="result-holder">
                <div
                    v-for="(result, i) in results.quiz_questions
                        .taken_questions"
                    :key="i"
                    class="result"
                >
                    <div class="type">
                        <div
                            v-if="result.correct_or_wrong == 'correct'"
                            class="correct"
                        >
                            Correct
                        </div>
                        <div
                            v-if="result.correct_or_wrong == 'wrong'"
                            class="incorrect"
                        >
                            incorrect
                        </div>
                    </div>
                    <div class="res">
                        <span>{{ i + 1 }}. {{ result.question.question }}</span>
                        {{ result.content }}
                        >
                        <span v-for="(answer, i) in result.answers" class="correct-answer">
                              <div
                                  v-if="answer.correct"
                                  class="correct"
                              >
                            correct
                        </div>
                                <div
                                    v-else
                                    class="incorrect"
                                >
                            incorrect
                        </div>

                            {{
                            answer.given_answer.content
                        }}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="d-flex next-btn">
            <a href="/quizzes" class="default-btn mr-4">Take Quiz</a>
            <a href="/lessons" class="default-btn">Next: Rewatch Video</a>
        </div>
    </div>
</template>
<script>
export default {
    props: ["quiz"],
    data: () => ({
        results: {
            quiz_questions: {}
        },
        options: {
            text: {
                color: "black",
                shadowEnable: true,
                shadowColor: "#000000",
                fontSize: 40,
                fontFamily: "Helvetica",
                dynamicPosition: false,
                hideText: false
            },
            progress: {
                color: "#D79D3E",
                backgroundColor: "#F4E4C7"
            },
            layout: {
                height: 250,
                width: 250,
                verticalTextAlign: 130,
                horizontalTextAlign: 95,
                zeroOffset: 0,
                strokeWidth: 10,
                progressPadding: 0,
                type: "circle"
            }
        }
    }),
    computed: {},
    created() {
        this.getResults();
    },
    methods: {
        getResults() {
            axios
                .get(`/quiz/get-results/${this.quiz}`)
                .then(response => {
                    this.results = response.data;
                    // console.log(this.results.quiz_questions.taken_questions);
                })
                .catch(err => {
                    console.log(err);
                });
        }
    }
};
</script>
