                            >Infectious diseases</label
                        >
                    </div>
                </div>
            </div>
            <div class="dropdown sort">
                <button
                    class="btn  dropdown-toggle"
                    type="button"
                    id="dropdownMenuButton"
                    data-toggle="dropdown"
